import React, { Component } from 'react';

import './style-prices-preview.css';
import './style-prices.css';

class Prices extends Component {
  componentDidMount() {
    window.scrollTo(0, 0);
  }

  render() {
    return (
      <main>
        <section className="prices-preview">
          <div className="prices-preview__wrapper">
            <h2 className="prices-preview__title">Цены на услуги</h2>
          </div>
        </section>
        <section className="prices">
          <div className="prices__wrapper">
            <h2 className="visually-hidden">Таблица цен на все услуги</h2>
            <table className="prices__table">
              <caption className="visually-hidden">Описание и цены всех услуг</caption>
              <tbody>
                <tr className="prices__table-tr  prices__table-tr--title">
                  <th className="prices__table-th" colSpan="2">Маникюр</th>
                </tr>
                <tr className="prices__table-tr  prices__table-tr--dark">
                  <td className="prices__table-td">Комбинированный маникюр + выравнивание / укрепление ногтевой пластины + покрытие гель-лаком (без дизайна)</td>
                  <td className="prices__table-td  prices__table-td--prices">500 <span className="prices__table-rub">&#x20BD;</span></td>
                </tr>
                <tr className="prices__table-tr">
                  <td className="prices__table-td">Комбинированный маникюр + выравнивание / укрепление ногтевой пластины + покрытие гель-лаком (с дизайном)</td>
                  <td className="prices__table-td  prices__table-td--prices">600 <span className="prices__table-rub">&#x20BD;</span></td>
                </tr>
                <tr className="prices__table-tr  prices__table-tr--dark">
                  <td className="prices__table-td">Комбинированный маникюр без покрытия</td>
                  <td className="prices__table-td  prices__table-td--prices">250 <span className="prices__table-rub">&#x20BD;</span></td>
                </tr>
                <tr className="prices__table-tr">
                  <td className="prices__table-td">Снятие гель-лака (моя работа) с последующим покрытием</td>
                  <td className="prices__table-td  prices__table-td--prices">Бесплатно</td>
                </tr>
                <tr className="prices__table-tr  prices__table-tr--dark">
                  <td className="prices__table-td">Снятие гель-лака (моя работа)</td>
                  <td className="prices__table-td  prices__table-td--prices">100 <span className="prices__table-rub">&#x20BD;</span></td>
                </tr>
                <tr className="prices__table-tr">
                  <td className="prices__table-td">Снятие гель-лака (работа другого мастера)</td>
                  <td className="prices__table-td  prices__table-td--prices">150 <span className="prices__table-rub">&#x20BD;</span></td>
                </tr>
                <tr className="prices__table-tr  prices__table-tr--dark">
                  <td className="prices__table-td">Снятие обычного лака</td>
                  <td className="prices__table-td  prices__table-td--prices">50 <span className="prices__table-rub">&#x20BD;</span></td>
                </tr>
                <tr className="prices__table-tr">
                  <td className="prices__table-td">Укрепление ногтей акриловой пудрой</td>
                  <td className="prices__table-td  prices__table-td--prices">150 <span className="prices__table-rub">&#x20BD;</span></td>
                </tr>
                <tr className="prices__table-tr  prices__table-tr--title">
                  <th className="prices__table-th" colSpan="2">Ресницы</th>
                </tr>
                <tr className="prices__table-tr  prices__table-tr--dark">
                  <td className="prices__table-td">Классическое наращивание ресниц</td>
                  <td className="prices__table-td  prices__table-td--prices">750 <span className="prices__table-rub">&#x20BD;</span></td>
                </tr>
                <tr className="prices__table-tr">
                  <td className="prices__table-td">Снятие ресниц (моя работа) без последующего наращивания</td>
                  <td className="prices__table-td  prices__table-td--prices">150 <span className="prices__table-rub">&#x20BD;</span></td>
                </tr>
                <tr className="prices__table-tr  prices__table-tr--dark">
                  <td className="prices__table-td">Снятие ресниц (работа другого мастера)</td>
                  <td className="prices__table-td  prices__table-td--prices">200 <span className="prices__table-rub">&#x20BD;</span></td>
                </tr>
                <tr className="prices__table-tr">
                  <td className="prices__table-td">Окрашивание ресниц</td>
                  <td className="prices__table-td  prices__table-td--prices">300 <span className="prices__table-rub">&#x20BD;</span></td>
                </tr>
                <tr className="prices__table-tr  prices__table-tr--title">
                  <th className="prices__table-th" colSpan="2">Брови</th>
                </tr>
                <tr className="prices__table-tr  prices__table-tr--dark">
                  <td className="prices__table-td">Моделирование формы бровей + коррекция + окрашивание бровей краской</td>
                  <td className="prices__table-td  prices__table-td--prices">500 <span className="prices__table-rub">&#x20BD;</span></td>
                </tr>
                <tr className="prices__table-tr">
                  <td className="prices__table-td">Моделирование формы бровей + коррекция (без окрашивания)</td>
                  <td className="prices__table-td  prices__table-td--prices">350 <span className="prices__table-rub">&#x20BD;</span></td>
                </tr>
              </tbody>
            </table>
          </div>
        </section>
      </main>
    );
  }
}

export default Prices;


