import { connect } from 'react-redux';
import React, { Component, Fragment } from 'react';
import { Route, Switch } from 'react-router-dom';
import { withRouter } from 'react-router-dom';
import axios from 'axios';

import '../../scaffolding.css';
import '../../normalize.css';
import { headerScroll } from '../../utils/utils';
import { homeHashAndScrollBlocks, updatestate, changeHashUrl } from '../../utils/homeHashAndScrollBlocks';
import Home from '../Home';
import Jobs from '../Jobs';
import Prices from '../../components/Prices';
import Header from '../../components/Header';
import Footer from '../../components/Footer';

class App extends Component {
  componentDidMount() {
    const { setJobs } = this.props;
    axios.get('http://5b3cc8b995bf8d0014a1d6ba.mockapi.io/jobs').then(({data}) => {
      setJobs(data);
    });
    if (window.location.hash) {
      updatestate(window.location.hash)
    }
    changeHashUrl();
    homeHashAndScrollBlocks();
  }

  componentDidUpdate() {
    if (window.location.hash) {
      updatestate(window.location.hash)
    }
  }

  render() {
    return (
      <Fragment>
        <Header />
        <Switch>
          <Route exact path="/" component={Home} />
          <Route exact path="/jobs" component={Jobs} />
          <Route exact path="/jobs/:category" component={Jobs} />
          <Route exact path="/jobs/:category/:id" component={Jobs} />
          <Route exact path="/prices" component={Prices} />
          <Route render={ () => <p><br/><br/><br/>Страница не найдена</p> } />
        </Switch>
        <Footer />
      </Fragment>
    );
  }
}

const mapDispatchToProps = (dispatch) => ({
  setJobs: (data) => dispatch({
    type: 'SET_JOBS',
    payload: data
  })
});

export default withRouter(connect(null, mapDispatchToProps)(App));


