// Функция измерения ширины скроллбара у браузера
export const widthScrollBrowser = () => {
  var div = document.createElement('div');
  div.style.overflowY = 'scroll';
  div.style.width = '50px';
  div.style.height = '50px';
  div.style.visibility = 'hidden';
  document.body.appendChild(div);
  var scrollWidth = div.offsetWidth - div.clientWidth;
  document.body.removeChild(div);
  return scrollWidth;
};

// Функция Деккоратор-Тормозилка
export const throttle = (func, ms) => {
	var isThrottled = false,
      savedArgs,
      savedThis;

  function wrapper() {
    if (isThrottled) { // (2)
      savedArgs = arguments;
      savedThis = this;
      return;
    }

    func.apply(this, arguments); // (1)
    isThrottled = true;

    setTimeout(function() {
      isThrottled = false; // (3)
      if (savedArgs) {
        wrapper.apply(savedThis, savedArgs);
        savedArgs = savedThis = null;
      }
    }, ms);
  }

  return wrapper;	
};

// Функция с помощью которой узнаем расстояние до определенного элемента в DOM-дереве от самого верха страницы
export const getCoords = (element) => {
  let box = element.getBoundingClientRect();
  let top = box.top + window.pageYOffset;
  return top;
};

// Функция склонения числительных, выставление правильного окончания у существительных
export const declinationOfNumerals = (number = 0, titles) => {
  const cases = [2, 0, 1, 1, 1, 2];
  return titles[ (number%100>4 && number%100<20)? 2 : cases[(number%10<5)?number%10:5] ];
};


