import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

import './style.css'

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpenMenu: false,
    };
    this.toggleViewMenu = this.toggleViewMenu.bind(this);
    this.toggleClassNameLinkHashToHeader = this.toggleClassNameLinkHashToHeader.bind(this);
  }

  toggleViewMenu() {
    this.setState({
      isOpenMenu: !this.state.isOpenMenu,
    })
  }

  toggleClassNameLinkHashToHeader(hash) {
    return window.location.hash === hash ? '  page-header__nav-item-link--active' : ''
  }

  render() {
    const { isOpenMenu } = this.state;
    console.log(window.location.pathname);
    return (
      <header className={ isOpenMenu ? 'page-header  page-header__nav--opened' : 'page-header  page-header__nav--closed' }>
        <h1 className="visually-hidden">Мастер по услугам косметологии, маникюру, ресницам, бровям</h1>
        <div className="page-header__nav-top">
          <div className="page-header__wrapper">
            <div className="page-header__wrapper-one">
              <button className="page-header__feedback" type="button">
                <span className="visually-hidden">Написать</span>
                <span className="page-header__popup-info  page-header__popup-info--email">Связаться по email</span>
              </button>
              <a className="social-button" href="//www.instagram.com/antonova.nail/" target="_blank">
                <span className="visually-hidden">Инстаграм</span>
                <svg xmlns="http://www.w3.org/2000/svg" height="29" viewBox="0 0 512 512">
                  <g>
                    <g>
                      <path fill="#607282" d="M363.273,0H148.728C66.719,0,0,66.719,0,148.728v214.544C0,445.281,66.719,512,148.728,512h214.544
                        C445.281,512,512,445.281,512,363.273V148.728C512,66.719,445.281,0,363.273,0z M472,363.272C472,423.225,423.225,472,363.273,472
                        H148.728C88.775,472,40,423.225,40,363.273V148.728C40,88.775,88.775,40,148.728,40h214.544C423.225,40,472,88.775,472,148.728
                        V363.272z"/>
                    </g>
                  </g>
                  <g>
                    <g>
                      <path fill="#607282" d="M256,118c-76.094,0-138,61.906-138,138s61.906,138,138,138s138-61.906,138-138S332.094,118,256,118z M256,354
                        c-54.037,0-98-43.963-98-98s43.963-98,98-98s98,43.963,98,98S310.037,354,256,354z"/>
                    </g>
                  </g>
                  <g>
                    <g>
                      <circle fill="#607282" cx="396" cy="116" r="20"/>
                    </g>
                  </g>
                </svg>
                <span className="page-header__popup-info  page-header__popup-info--insta">Связаться в Instagram</span>
              </a>
            </div>
            <div className="page-header__logo"><span className="visually-hidden">Логотип MyCosmet</span></div>
            <button onClick={this.toggleViewMenu} className="page-header__toggle" type="button"><span className="page-header__toggle-hamburger"></span>Открыть меню</button>
          </div>
        </div>
        <nav className="page-header__nav">
          <div className="page-header__nav-wrapper">
            <ul className="page-header__nav-list">
              <li className="page-header__nav-item">
                <NavLink className={`page-header__nav-item-link  page-header__nav-item-link--scroll${this.toggleClassNameLinkHashToHeader('#manicure')}`} to="/#manicure">Маникюр</NavLink>
              </li>
              <li className="page-header__nav-item">
                <NavLink className={`page-header__nav-item-link  page-header__nav-item-link--scroll${this.toggleClassNameLinkHashToHeader('#lashes')}`} to="/#lashes">Ресницы</NavLink>
              </li>
              <li className="page-header__nav-item">
                <NavLink className={`page-header__nav-item-link  page-header__nav-item-link--scroll${this.toggleClassNameLinkHashToHeader('#brow')}`} to="/#brow">Брови</NavLink>
              </li>
              <li className="page-header__nav-item">
                <NavLink className={`page-header__nav-item-link  page-header__nav-item-link--scroll${this.toggleClassNameLinkHashToHeader('#cosmetology')}`} to="/#cosmetology">Косметология</NavLink>
              </li>
              <li className="page-header__nav-item">
                <NavLink className="page-header__nav-item-link" activeClassName="page-header__nav-item-link--active" to="/jobs">Галерея работ</NavLink>
              </li>
              <li className="page-header__nav-item">
                <NavLink className="page-header__nav-item-link" activeClassName="page-header__nav-item-link--active" to="/prices">Цены</NavLink>
              </li>
            </ul>
          </div>
        </nav>
      </header>
    );
  }
}

export default Header;



