import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import './style-filter.css';
import './style-gallery.css';
import Job from '../Job';
import ModalJob from '../../containers/ModalJob';
import { widthScrollBrowser } from '../../utils/utils';

class Jobs extends Component {
  constructor(props) {
    super(props);
    this.state = {
      widthScrollbar: 0,
    };
  }

  componentDidMount() {
    window.scrollTo(0, 0);
    this.setState({
      widthScrollbar: widthScrollBrowser()
    });
  }

  componentDidUpdate() {
    const { id } = this.props;
    const { widthScrollbar } = this.state;
    if (id) {
      document.querySelector('body').classList.add('body-noscroll');
      document.querySelector('body').style.marginRight = `${widthScrollbar}px`;
      document.querySelector('.page-header__wrapper').style.paddingRight = `${widthScrollbar + 10}px`;
      document.querySelector('.page-header__nav-wrapper').style.paddingRight = `${widthScrollbar + 10}px`;
    } else {
      document.querySelector('body').classList.remove('body-noscroll');
      document.querySelector('body').style.marginRight = '0';
      document.querySelector('.page-header__wrapper').style.paddingRight = '10px';
      document.querySelector('.page-header__nav-wrapper').style.paddingRight = '10px';
    }
  }

  render() {
    const { jobs, filter, id, isReady, history } = this.props;
    return (
      <main>
        <section className="filter">
          <div className="filter__wrapper">
            <h2 className="filter__title">Галерея работ</h2>
            <ul className="filter__nav-list">
              <li className="filter__nav-item">
                <Link to="/jobs" className={ filter === undefined || filter === 'all' ? 'button filter__button  filter__button--active' : 'button filter__button' }>Все</Link>
              </li>
              <li className="filter__nav-item">
                <Link to={`/jobs/manicure`} className={ filter === 'manicure' ? 'button filter__button  filter__button--active' : 'button filter__button' }>Маникюр</Link>
              </li>
              <li className="filter__nav-item">
                <Link to={`/jobs/lashes`} className={ filter === 'lashes' ? 'button filter__button  filter__button--active' : 'button filter__button' }>Ресницы</Link>
              </li>
              <li className="filter__nav-item">
                <Link to={`/jobs/brow`} className={ filter === 'brow' ? 'button filter__button  filter__button--active' : 'button filter__button' }>Брови</Link>
              </li>
              <li className="filter__nav-item">
                <Link to={`/jobs/cosmetologist`} className={ filter === 'cosmetologist' ? 'button filter__button  filter__button--active' : 'button filter__button' }>Косметология</Link>
              </li>
            </ul>
          </div>
        </section>
        <section className="gallery">
          <div className="gallery__wrapper">
            <h2 className="visually-hidden">Фотографии всех работ по категориям</h2>
              <ul className="gallery__list">
                {
                  isReady ?
                    (jobs.map((item) =>
                      <Job key={item.id} item={item} category={filter} />
                    )) :
                    <div className="services__loader  services__loader--page-jobs">
                      <p className="services__loader-text  services__loader-text--dark">Загрузка...</p>
                      <img src="/img/spinner.gif" width="70" height="70" alt="Прелоадер"/>
                    </div>
                }
              </ul>
          </div>
        </section>
        { id === undefined ? null : <ModalJob isOpen id={id} filter={filter} jobs={jobs} history={history} /> }
      </main>
    );
  }
}

export default Jobs;


