import React, { Component, Fragment } from 'react';

import './style.css';

class ModalJob extends Component {
  constructor(props) {
    super(props);
    this.findJobById = this.findJobById.bind(this);
    this.findProperty = this.findProperty.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.clickOverlayCloseModal = this.clickOverlayCloseModal.bind(this);
    this.escCloseModal = this.escCloseModal.bind(this);
    this.createNode = this.createNode.bind(this);
  }

  componentDidMount() {
      document.addEventListener('keydown', this.escCloseModal);
  }

  componentWillUnmount() {
      document.removeEventListener('keydown', this.escCloseModal);
  }

  findJobById(property) {
    const { jobs, id } = this.props;
    const arr = jobs.filter(item => item.id === id);
    return arr.map(job => this.findProperty(job, property));
  }

  findProperty(obj, property) {
    switch(property) {
      case 'description':
        return obj.description;
      case 'category':
        return obj.category_modal;
      case 'id':
        return obj.id;
      case 'images':
        return obj.picture_full;
      default:
        return obj;
    }
  }

  closeModal(e) {
    const { filter } = this.props;
    e.stopPropagation();
    this.props.history.push(`/jobs/${filter}`);
  }

  clickOverlayCloseModal(e) {
    if (e.target.className === 'modal-job') {
      this.closeModal(e);
    }
  }

  createNode() {
    const objSlide = this.findJobById('images')[0];
    console.log('11', this.findJobById('images')[0]);
    if (objSlide) {
      return (
      <picture>
        <source className="desktop-img" media="(min-width: 1200px)" srcSet={`${objSlide[0].desktop_1x}, ${objSlide[0].desktop_2x}`} />
        <img className="mobile-img" src="#" srcSet="#" width="640" height="480" alt="Фотография работы" />
      </picture>
      );
    }
  }

  escCloseModal(e) {
    if (e.keyCode === 27) {
      this.closeModal(e);
    }
  }

  render() {
    const { isOpen, isReady } = this.props;
    return (
      <section className={ isOpen ? 'modal-job' : 'modal-job  hidden' } onClick={this.clickOverlayCloseModal} >
        <h2 className="visually-hidden">Подробная информация о проделанной работе</h2>
        <div className="modal-job__popup">
          {
            isReady ?
              <Fragment>
                  <div className="modal-job__wrapper-one">
                      {this.createNode()}
                  </div>
                  <div className="modal-job__wrapper-two">
                      <p className="modal-job__description">{this.findJobById('description')}</p>
                      <p className="modal-job__category">{`${this.findJobById('category')} ${this.findJobById('id')}`}</p>
                  </div>
              </Fragment> :
              <div className="services__loader  services__loader--modal-job">
                  <p className="services__loader-text  services__loader-text--dark">Загрузка...</p>
                  <img src="/img/spinner.gif" width="70" height="70" alt="Прелоадер"/>
              </div>
          }
          <button className="modal-job__close" type="button" onClick={this.closeModal}>Закрыть</button>
        </div>
      </section>
    );
  }
}

export default ModalJob;


