import React, { Component } from 'react';

import './style.css';

class Footer extends Component {
  render() {
    return (
      <footer className="page-footer">
        <div className="page-footer__wrapper">
          <div className="page-footer__wrapper-two">
            <div className="page-footer__logo"><span className="visually-hidden">Логотип MyCosmet</span></div>
            <div className="page-footer__wrapper-btn">
              <a className="button  page-footer__btn  page-footer__btn-email" href="#">Связаться по Email</a>
              <a className="button  page-footer__btn" href="//www.instagram.com/antonova.nail/" target="_blank">Связаться в Instagram</a>
            </div>
          </div>
          <div className="page-footer__wrapper-free">
            <p className="page-footer__copyright">© 2018 <span className="page-footer__copyright--small">mycosmet.ru</span></p>
            <p className="page-footer__city">г. Тольятти</p>
            <p className="page-footer__develop"><span className="page-footer__develop--small">Разработано:</span> <a href="//samsonpro.ru/" target="_blank">samsonpro.ru</a></p>
          </div>
        </div>
      </footer>
    );
  }
}

export default Footer;


