import  { throttle, getCoords } from './utils';

let bodi = document.querySelector('body');
let liLink;
let liLinkActive;
let state;
let urlHash = [
  '#manicure',
  '#lashes',
  '#brow',
  '#cosmetology',
  '/',
];

// Функция проверки hash'а на правильность, сравнение с имеющимися hash'ами на странице
export const forEachUrlHash = (hash) => {
  if (urlHash[0] === hash || urlHash[1] === hash || urlHash[2] === hash || urlHash[3] === hash || urlHash[4] === hash) {
    return true;
  } else {
    window.scrollTo(0, 0);
    console.log('Ошибка в url');
    return false;
  }
};

// Функция updatestate для HashBang, обновление состояния страницы
export const updatestate = (hash) => {
  let result = forEachUrlHash(hash);
  if (!result) {
    return;
  } else {
    let elem = document.querySelector(hash);
    console.log('----555');
    let scrolling = getCoords(elem) - 186;
    console.log(scrolling);
    window.scrollTo(0, scrolling);
  }
};

export const changeHashUrl = () => {
  return window.addEventListener('hashchange', function(e) {
    e.preventDefault();
    if (window.location.hash === '' || window.location.hash === undefined || window.location.hash === null) {
      window.scrollTo(0, 0);
      return;
    }
    console.log('--WWWW');
    updatestate(window.location.hash);
  });
}

export const homeHashAndScrollBlocks = () => {
  liLink = document.querySelectorAll('.page-header__nav-item-link--scroll');
  if (!liLink) {
    return;
  }

  // Обработчик скролла страницы обернутый в функцию Деккоратор-тормозилка
  window.onscroll = throttle(function() {
    let header = document.querySelector('.page-header');
    // Добавление тени у header
    if (window.pageYOffset > 0 || document.documentElement.scrollTop > 0) {
      header.classList.add('page-header--scroll');
    } else {
      if (header.classList.contains('page-header--scroll')) {
        header.classList.remove('page-header--scroll');
      }
    }

    const hash = window.location.hash;
    const pathname = window.location.pathname;
    if (!hash && pathname !== '/') {
      return;
    }

    if (window.pageYOffset > 0 && window.pageYOffset < 451) {
      if (liLinkActive !== undefined) {
        liLinkActive.classList.remove('page-header__nav-item-link--active');
      }
      let hash = null;
      let res = window.history.state; // Узнаем какой хеш уже записан в History, чтобы не повторяться
      if (res !== null) { // Если уже записанный хеш не совпадает с хешем нажатой ссылки, то этот новый хеш записываем в History
        state = null;
        window.history.pushState(state, '', '/');
        console.log('Запись');
      }
    }

    if (window.pageYOffset > 451 && window.pageYOffset < 1292) {
      if (liLinkActive !== undefined) {
        liLinkActive.classList.remove('page-header__nav-item-link--active');
      }
      liLink[0].classList.add('page-header__nav-item-link--active');
      liLinkActive = liLink[0];
      let hash = '#manicure';
      let res = window.history.state; // Узнаем какой хеш уже записан в History, чтобы не повторяться
      if (res === null || res.page !== hash) { // Если уже записанный хеш не совпадает с хешем нажатой ссылки, то этот новый хеш записываем в History
        state = { page: hash };
        window.history.pushState(state, '', state.page);
        console.log('Запись');
      }
    }

    if (window.pageYOffset > 1292 && window.pageYOffset < 2133) {
      if (liLinkActive !== undefined) {
        liLinkActive.classList.remove('page-header__nav-item-link--active');
      }
      liLink[1].classList.add('page-header__nav-item-link--active');
      liLinkActive = liLink[1];
      let hash = '#lashes';
      let res = window.history.state; // Узнаем какой хеш уже записан в History, чтобы не повторяться
      if (res === null || res.page !== hash) { // Если уже записанный хеш не совпадает с хешем нажатой ссылки, то этот новый хеш записываем в History
        state = { page: hash };
        window.history.pushState(state, '', state.page);
        console.log('Запись');
      }
    }

    if (window.pageYOffset > 2133 && window.pageYOffset < 2974) {
      if (liLinkActive !== undefined) {
        liLinkActive.classList.remove('page-header__nav-item-link--active');
      }
      liLink[2].classList.add('page-header__nav-item-link--active');
      liLinkActive = liLink[2];
      let hash = '#brow';
      let res = window.history.state; // Узнаем какой хеш уже записан в History, чтобы не повторяться
      if (res === null || res.page !== hash) { // Если уже записанный хеш не совпадает с хешем нажатой ссылки, то этот новый хеш записываем в History
        state = { page: hash };
        window.history.pushState(state, '', state.page);
        console.log('Запись');
      }
    }

    if (window.pageYOffset > 2974 && window.pageYOffset < 3831) {
      if (liLinkActive !== undefined) {
        liLinkActive.classList.remove('page-header__nav-item-link--active');
      }
      liLink[3].classList.add('page-header__nav-item-link--active');
      liLinkActive = liLink[3];
      let hash = '#cosmetology';
      let res = window.history.state; // Узнаем какой хеш уже записан в History, чтобы не повторяться
      if (res === null || res.page !== hash) { // Если уже записанный хеш не совпадает с хешем нажатой ссылки, то этот новый хеш записываем в History
        state = { page: hash };
        window.history.pushState(state, '', state.page);
        console.log('Запись');
      }
    }
  }, 200);

}


