const initialState = {
  items: [],
  isReady: false,
};

export default function(state = initialState, action) {
  switch(action.type) {
    case 'SET_JOBS':
      return {
        ...state,
        items: action.payload,
        isReady: true
      };
    default:
      return state;
  }
};


