import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import { declinationOfNumerals } from '../../utils/utils';

import './style-advantages.css';
import './style-services.css';

class Home extends Component {
  constructor(props) {
    super(props);
    this.findExecutedJobs = this.findExecutedJobs.bind(this);
    this.numberOfJobs = this.numberOfJobs.bind(this);
  }

  componentDidMount() {
    window.scrollTo(0, 0);
  }

  findExecutedJobs(categoryString) {
    const { jobs } = this.props;
    return jobs.filter(job => job.category === categoryString);
  }

  numberOfJobs(categoryString) {
    const { isReady } = this.props;
    return isReady ? this.findExecutedJobs(categoryString).length : <span>&infin;</span>;
  }

  render() {
    const arrayJobsString = ['работа', 'работы', 'работ'];

    return (
      <main>
        <section className="advantages">
          <h2 className="visually-hidden">Список всех наших преимуществ, чтобы довериться нам</h2>
          <div className="advantages__wrapper">
            <ul className="advantages__list">
              <li className="advantages__item">
                <h3 className="advantages__item-title">Низкие цены</h3>
                <p className="advantages__item-paragraph"><span className="advantages__item-paragraph--marker">Стоимость</span> <span className="advantages__item-paragraph--marker">услуг</span> значительно <span className="advantages__item-paragraph--marker">ниже</span>, чем в косметологических салонах города</p>
              </li>
              <li className="advantages__item">
                <h3 className="advantages__item-title">Ваше доверие</h3>
                <p className="advantages__item-paragraph">Не <span className="advantages__item-paragraph--marker">каждый</span> <span className="advantages__item-paragraph--marker">доверит</span> свое тело не <span className="advantages__item-paragraph--marker">знакомому</span> <span className="advantages__item-paragraph--marker">косметологу</span>, но данный случай исключение</p>
              </li>
              <li className="advantages__item">
                <h3 className="advantages__item-title">Большой опыт</h3>
                <p className="advantages__item-paragraph">Имеется <span className="advantages__item-paragraph--marker">медицинское</span> <span className="advantages__item-paragraph--marker">образование</span> и <span className="advantages__item-paragraph--marker">сертификаты</span> под все виды предоставленных услуг</p>
              </li>
              <li className="advantages__item">
                <h3 className="advantages__item-title">Свой подход</h3>
                <p className="advantages__item-paragraph"><span className="advantages__item-paragraph--marker">Ценим</span> мнение и желание клиента, <span className="advantages__item-paragraph--marker">подскажем</span> и <span className="advantages__item-paragraph--marker">подкорректируем</span> ваши мысли</p>
              </li>
            </ul>
          </div>
        </section>
        <section className="services  services--dark">
          <div className="services__wrapper">
            <h2 className="services__title  services__title--dark"><span>Ма</span><br/>ни<br/>кюр</h2>
            <div className="services__wrapper-one">
              <p className="services__number  services__number--dark">01</p>
              <p className="services__number-big">01</p>
              <p className="services__jobs  services__jobs--dark"><span className="services__jobs-numbers  services__jobs-numbers--dark  services__jobs-numbers--manicure">{ this.numberOfJobs('manicure') }</span> { declinationOfNumerals(this.numberOfJobs('manicure'), arrayJobsString) }</p>
            </div>
            <h3 className="services__title-two  services__title-two--dark" id="manicure">Ногтевой</h3>
            <p className="services__title-free  services__title-free--dark">Сервис</p>
            <p className="services__description  services__description--dark">Предлагаем все виды профессионального маникюра, такие как аппаратный, обрезной или комбинированный маникюр, укрепление ногтей, выравнивание ногтевой пластины, дизайн, коррекция, ремонт и снятие покрытия. Имеется все необходимое оборудование для комфорта во время процедуры, современные инструменты и эффективные косметические средства.</p>
            <div className="services__loader">
              <p className="services__loader-text">Загрузка...</p>
              <img src="/img/spinner.gif" width="70" height="70" alt="Прелоадер"/>
            </div>
            
            
            <div className="services__wrapper-two">
              <Link className="button  services__jobs-btn" to="/jobs/manicure">Все работы</Link>
              <Link className="button  services__price-btn  services__price-btn--dark" to="/prices">Цены</Link>
            </div>
          </div>
        </section>
        <section className="services">
          <div className="services__wrapper">
            <h2 className="services__title"><span>Рес</span><br/>ни<br/>цы</h2>
            <div className="services__wrapper-one">
              <p className="services__number">02</p>
              <p className="services__number-big">02</p>
              <p className="services__jobs"><span className="services__jobs-numbers  services__jobs-numbers--lashes">{ this.numberOfJobs('lashes') }</span> { declinationOfNumerals(this.numberOfJobs('lashes'), arrayJobsString) }</p>
            </div>
            <h3 className="services__title-two" id="lashes">Классическое</h3>
            <p className="services__title-free">Наращивание</p>
            <p className="services__description">Классическое наращивание универсально и является самым естественным наращиванием, на одну настоящую ресницу приклеивается одна искусственная. Идеальный вариант для первого знакомства с наращиванием, повторяет рост собственных ресниц удлиняя их и увеличивая густоту. Глаза становятся яркие и выразительные без видимого макияжа. Работа производится на материалах премиум класса.</p>
            <div className="services__loader">
              <p className="services__loader-text  services__loader-text--dark">Загрузка...</p>
              <img src="/img/spinner.gif" width="70" height="70" alt="Прелоадер"/>
            </div>
            <ul className="services__slider  hidden">
              <li className="services__slide">
                <img src="" width="" height="" alt="Превью работы"/>
                1
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt="Превью работы"/>
                2
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt="Превью работы"/>
                3
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt="Превью работы"/>
                4
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt="Превью работы"/>
                5
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt="Превью работы"/>
                6
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt="Превью работы"/>
                7
              </li>
            </ul>
            <div className="services__wrapper-two">
              <Link className="button  services__jobs-btn" to="/jobs/lashes">Все работы</Link>
              <Link className="button  services__price-btn" to="/prices">Цены</Link>
            </div>
          </div>
        </section>
        <section className="services  services--dark">
          <div className="services__wrapper">
            <h2 className="services__title  services__title--dark"><span>Бро</span><br/>ви</h2>
            <div className="services__wrapper-one">
              <p className="services__number  services__number--dark">03</p>
              <p className="services__number-big">03</p>
              <p className="services__jobs  services__jobs--dark"><span className="services__jobs-numbers  services__jobs-numbers--dark  services__jobs-numbers--brow">{ this.numberOfJobs('brow') }</span> { declinationOfNumerals(this.numberOfJobs('brow'), arrayJobsString) }</p>
            </div>
            <h3 className="services__title-two  services__title-two--dark" id="brow">Коррекция</h3>
            <p className="services__title-free  services__title-free--dark">И окрашивание</p>
            <p className="services__description  services__description--dark">Работа с бровями сложный и кропотливый труд, нужно знать все опорные точки и понимать как форма бровей будет смотреться на лице, а также знать строение кожи и волоса. Предлагаем окрашивание бровей профессиональной краской. В ее состав входят ухаживающие компоненты, такие как аргановое масло, витамин Е и другие. Краска эффективно окрашивает волоски, при этом не травмируя и не пересушивая волос.</p>
            <div className="services__loader">
              <p className="services__loader-text">Загрузка...</p>
              <img src="/img/spinner.gif" width="70" height="70" alt="Прелоадер"/>
            </div>
            <ul className="services__slider  hidden">
              <li className="services__slide">
                <img src="" width="" height="" alt="Превью работы"/>
                1
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt="Превью работы"/>
                2
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt="Превью работы"/>
                3
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt="Превью работы"/>
                4
              </li>
            </ul>
            <div className="services__wrapper-two">
              <Link className="button  services__jobs-btn" to="/jobs/brow">Все работы</Link>
              <Link className="button  services__price-btn  services__price-btn--dark" to="/prices">Цены</Link>
            </div>
          </div>
        </section>
        <section className="services">
          <div className="services__wrapper">
            <h2 className="services__title"><span>Косме</span><br/>толо<br/>гия</h2>
            <div className="services__wrapper-one">
              <p className="services__number">04</p>
              <p className="services__number-big">04</p>
              <p className="services__jobs"><span className="services__jobs-numbers  services__jobs-numbers--cosmet">{ this.numberOfJobs('cosmet') }</span> { declinationOfNumerals(this.numberOfJobs('brow'), arrayJobsString) }</p>
            </div>
            <h3 className="services__title-two" id="cosmetology">Эстетическая</h3>
            <p className="services__title-free">Косметология</p>
            <p className="services__description  services__description--cosmet">Эта область косметологии направленна на поддержание молодости и устранения признаков старения. Она включает в себя только терапевтическое лечение без хирургического вмешательства. Предлагаем широкий спектр процедур, выполняемых на специальном оборудовании с применением профессиональной косметики, такие как: массаж лица, лимфодренажный массаж тела, обертывания, маски для лица, пилинги, микронидлинг, механическая чистка лица, депиляция теплым воском.</p>
            <div className="services__loader">
              <p className="services__loader-text  services__loader-text--dark">Загрузка...</p>
              <img src="/img/spinner.gif" width="70" height="70" alt="Прелоадер"/>
            </div>
            <ul className="services__slider  hidden">
              <li className="services__slide">
                <img src="" width="" height="" alt="Превью работы"/>
                1
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt="Превью работы"/>
                2
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt="Превью работы"/>
                3
              </li>
            </ul>
            <div className="services__wrapper-two">
              <Link className="button  services__jobs-btn" to="/jobs/cosmetologist">Все работы</Link>
              <Link className="button  services__price-btn" to="/prices">Цены</Link>
            </div>
          </div>
        </section>
      </main>
    );
  }
}

export default Home;


