import { connect } from 'react-redux';

import Jobs from '../../components/Jobs';

const sortBy = (jobs, filterBy) => {
  switch(filterBy) {
    case 'manicure':
      return jobs.filter(job => job.category === 'manicure');
    case 'lashes':
      return jobs.filter(job => job.category === 'lashes');
    case 'brow':
      return jobs.filter(job => job.category === 'brow');
    case 'cosmetologist':
      return jobs.filter(job => job.category === 'cosmet');
    case 'all':
      return jobs;
    default:
      return jobs;
  }
}

const mapStateToProps = ( { jobs }, ownProps ) => ({
  jobs: jobs.items && sortBy(jobs.items, ownProps.match.params.category),
  filter: ownProps.match.params.category,
  id: ownProps.match.params.id,
  isReady: jobs.isReady,
});

export default connect(mapStateToProps, null)(Jobs);


